// >= 768 (1200版心显示)

@media screen and (min-width: 768px) {
  .item-img-come {
    width: 70px;
    height: 70px;
    background: #D9D8E0;
    border-radius: 50%;
    padding-top: 20px;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    color: rgba(109, 107, 121, 1);
    line-height: 16px;

    img {
      // width: 80%;
    }
  }

  .margin0a {
    margin: 0 auto !important;
    margin-bottom: 3px !important;
  }

  .mt40 {
    margin-top: 40px !important;
  }

  .card-wrap {
    width: 1200px;
    margin: 0 auto;
    margin-top: 100px;
    // background-color: red;

    .card-item1,
    .card-item2 {
      height: 340px;
      margin-bottom: 20px;

      .card-item-logo {
        .logo-wrap:nth-child(3n) {
          margin-right: 0;
        }
      }
    }

    .card-item {
      width: 590px;
      border: 1px solid #E9E8F2;
      float: left;
      padding-left: 20px;
      padding-right: 20px;

      .card-item-title {
        font-size: 28px;
        font-weight: bold;
        color: rgba(7, 2, 55, 1);
        height: 100px;
        line-height: 80px;
        position: relative;

        &::after {
          content: '';
          display: block;
          position: absolute;
          width: 40px;
          height: 4px;
          left: 5px;
          bottom: 25px;
          background-color: #D5A24E;
        }
      }

      .card-item-logo {
        // background-color: greenyellow;

        .logo-wrap {
          width: 172px;
          height: 60px;
          float: left;
          background-color: #F4F2FB;
          margin-right: 15px;
          margin-bottom: 15px;
          position: relative;
          border-radius: 30px;

          img {
            width: 60%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }

    .card-item2 {
      float: right;
    }

    .card-item3 {
      width: 1200px;
    }
  }

  .protocols-wrap {
    margin-top: 100px;
    padding-top: 80px;
    padding-bottom: 80px;
    width: 100%;
    // background-color: green;
    background: url('./images/bj-pro.png') no-repeat;
    background-size: 120%;
  }

  .Media-reports-wrap {
    margin-top: 100px;
    // padding-top: 80px;
    padding-bottom: 80px;
    width: 100%;
    // background-color: green;
    background: url('./images/bj-me.png') no-repeat;
    background-size: 110%;
  }

  .protocols {
    width: 1200px;
    margin: 0 auto;
    // background-color: red;

    .title {
      width: 100%;
      text-align: center;
      // background-color: green;
      margin: 0 auto;
      font-size: 28px;
      font-weight: bold;
      color: rgba(7, 2, 55, 1);
      height: 100px;
      line-height: 80px;
      position: relative;

      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 40px;
        height: 4px;
        left: 50%;
        bottom: 26px;
        background-color: #D5A24E;
        transform: translate(-50%, 0%);
      }
    }

    .protocols-monetary {
      .item-wrap {
        .item {
          min-height: 324px;
          width: 570px;
          margin-right: 60px;
          background-color: #fff;
          float: left;
          padding: 20px;
          background: rgba(255, 255, 255, 1);
          box-shadow: 5px 9px 30px 0px rgba(52, 52, 92, 0.08);
          border-radius: 6px;

          .item-img {
            img {
              width: 70px;
              height: 70px;
            }
          }

          .item-title {
            height: 45px;
            line-height: 50px;
            font-size: 24px;
            font-weight: bold;
            color: rgba(7, 2, 55, 1);
          }

          .item-title-plus {
            font-size: 16px;
            font-weight: 500;
            height: 40px;
            color: rgba(7, 2, 55, 1);
          }

          .item-details {
            font-size: 16px;
            font-weight: 200;
            color: rgba(148, 145, 170, 1);
            line-height: 28px;
            min-height: 84px;
          }

          .item-arrow {
            cursor: pointer;
            padding-top: 16px;
            font-size: 18px;
            font-weight: 500;
            color: rgba(215, 167, 88, 1);

            a {
              color: rgba(215, 167, 88, 1);
              text-decoration: none;
            }

            img {
              margin-left: 10px;
              width: 30px;
            }
          }
        }

        .item:nth-child(2n) {
          margin-right: 0;
        }
      }


    }

    .protocols-liquidity {
      .item-wrap {
        .item {
          background: rgba(255, 255, 255, 1);
          box-shadow: 5px 9px 30px 0px rgba(52, 52, 92, 0.08);
          border-radius: 6px;

          width: 386px;
          margin-right: 20px;
          background-color: #fff;
          float: left;
          padding: 20px;

          .item-img {
            height: 70px;
            text-align: center;

            img {
              width: 70px;
              margin-top: 12px;
            }
          }

          .item-title {
            text-align: center;
            height: 45px;
            line-height: 50px;
            font-size: 24px;
            font-weight: bold;
            color: rgba(7, 2, 55, 1);
          }

          .item-title-plus {
            text-align: center;
            font-size: 16px;
            font-weight: 500;
            height: 40px;
            color: rgba(7, 2, 55, 1);
          }

          .item-details {
            text-align: center;
            font-size: 16px;
            font-weight: 200;
            color: rgba(148, 145, 170, 1);
            line-height: 28px;
            min-height: 112px;
            // display: -webkit-box;
            // -webkit-box-orient: vertical;
            // -webkit-line-clamp: 3;
            // overflow: hidden;
          }

          .item-arrow {
            text-align: center;
            cursor: pointer;
            padding-top: 16px;
            font-size: 18px;
            font-weight: 500;
            color: rgba(215, 167, 88, 1);

            a {
              color: rgba(215, 167, 88, 1);
              text-decoration: none;
            }

            img {
              margin-left: 10px;
              width: 30px;
            }
          }
        }

        .item:nth-child(3n) {
          margin-right: 0;
        }
      }
    }

  }

  .Media-reports {
    padding-top: 80px;
    width: 1200px;
    margin: 0 auto;

    // background-color: #fff;
    .title {
      margin-bottom: 10px;
    }

    .Media-item {
      height: 80px;
      line-height: 80px;
      border-bottom: 1px solid #DDDCEB;
      position: relative;
      background-color: #fff;
      padding: 10px;

      .time {
        font-size: 14px;
        font-weight: 500;
        color: rgba(148, 145, 170, 1);
        display: inline-block;
        width: 140px;
      }

      .news {
        font-size: 16px;
        font-weight: 500;
        color: rgba(7, 2, 55, 1);
      }

      .pic {
        position: absolute;
        right: 10px;
        top: 6px;

        img {
          width: 12px;
        }
      }
    }
  }
}