// 
// >=768 1200版心显示
@media screen and (min-width: 768px) {

  .App {
    .title {
      // font-size: 28px;
      // font-weight: 500;
      // color: rgba(7, 2, 55, 1);

      width: 100%;
      text-align: center;
      // background-color: green;
      margin: 0 auto;
      font-size: 28px;
      font-weight: bold;
      color: rgba(7, 2, 55, 1);
      height: 100px;
      line-height: 80px;
      position: relative;

      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 40px;
        height: 4px;
        left: 50%;
        bottom: 26px;
        background-color: #D5A24E;
        transform: translate(-50%, 0%);
      }
    }

    .top-wrap {
      min-width: 1200px;
      height: 500px;
      background: url('./images/circle.png') no-repeat;
      background-size: 100% 100%;
      padding-top: 20px;

      .top {
        width: 1200px;
        // background-color: red;
        margin: 0 auto;

        .top-left {
          float: left;
        }

        .top-right {
          float: right;

          .top-right-box {
            float: left;
            position: relative;
            // background-color: green;
            padding-bottom: 15px;
            margin-right: 80px;

            .top-right-box-items {
              display: none;
              position: absolute;
              top: 30px;
              min-width: 160px;
              background-color: red;
              background: rgba(255, 255, 255, 1);
              border: 2px solid rgba(236, 236, 236, 1);
              box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.08);
              border-radius: 6px;
              padding: 5px 10px;

              a {
                display: block;
                text-decoration: none;
                font-size: 14px;
                font-weight: 200;
                color: rgba(148, 145, 170, 1);
                transition: 0.3s;
                line-height: 34px;

                &:hover {
                  color: #D5A24E;
                }
              }
            }
          }

          .top-right-box:hover {
            cursor: pointer;

            .top-right-box-items {
              display: block;
            }
          }
        }
      }

      .title-top {
        padding-top: 50px;
        width: 1200px;
        // background-color: palegoldenrod;
        margin: 0 auto;
        text-align: center;

        img {
          width: 60%;
        }
      }

      .text {
        width: 700px;
        padding-top: 10px;
        // background-color: red;
        margin: 0 auto;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        color: rgba(123, 117, 167, 1);
        line-height: 28px;
      }

      .connect {
        width: 1200px;
        // background-color: palegoldenrod;
        margin: 0 auto;
        text-align: center;
        background: url('./images/twopoint.png') no-repeat center center;
        background-size: 50%;
        margin-top: 80px;

        a {
          display: inline-block;
          margin: 0 13px;

          img {
            width: 32px;
            height: 32px;
          }
        }
      }
    }

    .DeFi-Full-Stacks {
      padding-top: 150px;
      width: 1200px;
      margin: 0 auto;
      // background-color: red;

      .DeFi-left {
        float: left;
        width: 650px;
        font-size: 16px;
        font-weight: 300 !important;
        color: rgba(148, 145, 170, 1);
        line-height: 24px;
        margin-top: 20px;
      }

      .DeFi-right {
        float: right;
        width: 550px;
        // background-color: palegoldenrod;
        position: relative;

        img {
          width: 440px;
          position: absolute;
          left: 50px;
          top: -65px;
        }
      }
    }

    .DeFi-Full-Stacks-mobile {
      display: none;
    }

    .Monetary-Protocols {
      padding-top: 150px;
      width: 1200px;
      margin: 0 auto;
      // background-color: paleturquoise;

      .Monetary-wrap {
        width: 1200px;
        padding-top: 50px;

        .Monetary-wrap-item {
          transition: 0.3s;
          width: 350px;
          height: 190px;
          float: left;
          margin-right: 75px;

          .item-img {
            // background-color: peachpuff;
            text-align: center;

            img {
              width: 60px;
            }
          }

          .item-title {
            text-align: center;
            line-height: 38px;
            font-size: 16px;
            color: rgba(7, 2, 55, 1);
          }

          .item-details {
            width: 80%;
            margin: 0 auto;
            text-align: center;
            font-size: 14px;
            font-weight: 300;
            color: rgba(148, 145, 170, 1);
            line-height: 20px;
            height: 65px;
            // background: red;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
          }

          .item-arrow {
            display: none;
          }

          &.last-item {
            margin-right: 0;
          }
        }

        .Monetary-wrap-item:hover {
          transition: 0.3s;
          background: rgba(255, 255, 255, 1);
          box-shadow: 0px 20px 30px 0px rgba(7, 2, 55, 0.1);
          border-radius: 4px;
          position: relative;
          overflow: hidden;

          &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            width: 100%;
            height: 4px;
            background-color: #D5A24E;
          }

          .item-title {
            margin-top: 10px;
          }

          .item-img {
            display: none;
          }

          .item-details {
            height: 108px;
            -webkit-line-clamp: 5;
          }

          .item-arrow {
            display: block;
            width: 40px;
            // background-color: green;
            margin: 0 auto;
            cursor: pointer;

            img {
              width: 100%;
            }
          }
        }
      }
    }

    .Features-wrap {
      padding-top: 80px;
      min-width: 1200px;
      // background-color: green;

      .Features {
        width: 1200px;
        margin: 0 auto;
        // background-color: red;

        .item-wrap {
          padding-top: 30px;

          .item {
            width: 350px;
            height: 185px;
            float: left;
            margin-right: 75px;
            float: left;
            // background-color: palegreen;
            margin-bottom: 80px;
            position: relative;

            .item-img {
              width: 120px;
              height: 120px;
              background: url('./images/bg-circle.png') no-repeat;
              background-size: 100% 100%;
              margin: 0 auto;
              // background-color: red;
              position: relative;

              img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 36%;
              }

              .item-img-img {
                width: 50%;
              }
            }

            .item-title {
              text-align: center;
              // line-height: 38px;
              font-size: 16px;
              color: rgba(7, 2, 55, 1);
            }

            .item-text {
              margin-top: 10px;
              position: relative;

              .item-text-t {
                position: absolute;
                top: 0;
                // background-color: pink;
                font-size: 14px;
                font-weight: 300;
                color: rgba(148, 145, 170, 1);
                line-height: 18px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;

                .img-up {
                  display: none;
                }
              }

              .item-text-t-active {
                height: auto;
                position: relative;
                font-size: 14px;
                font-weight: 300;
                color: rgba(148, 145, 170, 1);
                line-height: 18px;

                .img-up {
                  width: 20px;
                  position: absolute;
                  bottom: -30px;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  cursor: pointer;
                }
              }
            }

            .item-text-p {
              position: absolute;
              bottom: -26px;
              width: 20px;
              height: 20px;
              left: 50%;
              transform: translate(-10px, 0px);

              img {
                width: 20px;
                cursor: pointer;
              }
            }

            .item-text-p-active {
              display: none;
            }

            &:nth-child(3n) {
              margin-right: 0;
            }
          }
        }
      }
    }

    .box-wallet {
      padding-top: 80px;
      width: 1200px;
      margin: 0 auto;
      // background-color: red;


      .images-wrap {
        padding-top: 30px;

        div:nth-child(5n) {
          margin-right: 0;
        }
      }

      .div-image {
        width: 224px;
        height: 100px;
        // background-color: palegoldenrod;
        background-color: #fff;
        float: left;
        margin-right: 20px;
        margin-bottom: 20px;
        position: relative;
        border: 1px solid #E7E6F5;

        img {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 80%;
        }
      }
    }

    
  }
}

