// 
// >=768 1200版心显示
@media screen and (min-width: 768px) {
  .App {
    .foot {
      padding-top: 150px;
      width: 1200px;
      // background-color: red;
      margin: 0 auto;
      padding-bottom: 30px;

      .foot-item {
        float: left;
        width: 270px;
        // background-color: palegoldenrod;

        .foot-item-title {
          font-size: 16px;
          font-weight: 500;
          color: rgba(7, 2, 55, 1);
          height: 36px;
        }

        .foot-item-content {
          width: 300px;
          font-size: 14px;
          font-weight: 400;
          color: rgba(148, 145, 170, 1);
          height: 26px;

          a {
            text-decoration: none;
            font-size: 14px;
            font-weight: 400;
            color: rgba(148, 145, 170, 1);
            transition: 0.3s;

            &:hover {
              color: #D5A24E;
            }

            position: relative;

            .weixin-img {
              display: none;
              position: absolute;
              width: 80px;
              left: 20px;
              bottom: 20px;
            }
          }

          a.weixin-img-wrap:hover {
            .weixin-img {
              display: block;
            }
          }
        }

        .footer-right-fixed:hover {
          .fixed2 {
            display: block;
          }
        }

        .footer-right-fixed {
          padding-top: 15px;
          position: relative;
          cursor: pointer;

          .fixed1 {
            float: left;
          }

          .fixed2 {
            display: none;
            position: absolute;
            bottom: 5px;
            background: #FFF;
            width: 90px;
            box-shadow: 0px 0px 35px 0px rgba(94, 85, 126, 0.15);
            /* border-radius: 6px; */
            left: -20px;

            ul {
              margin-bottom: 0;

              li {
                text-align: center;
                line-height: 40px;

                &:hover {
                  background: #F0EEFF;
                  cursor: pointer;
                }
              }

              li:nth-child(1) {
                border-bottom: 1px solid #DDDDE8;
              }
            }
          }

          .fixed-img {
            float: left;
            margin-left: 10px;

            img {
              width: 10px;
            }
          }

          ul,
          li {
            list-style: none;
          }
        }

      }

      .icom-a {
        a {
          margin-right: 15px;

          img {
            width: 20px;
          }
        }
      }
    }
  }
}

// <768 
@media screen and (max-width: 767px) {
  .App {
    .foot {
      padding-top: 50px;
      // width: 1200px;
      // background-color: red;
      width: 90%;
      margin: 0 auto;
      padding-bottom: 30px;

      .foot-item {
        // float: left;
        // width: 270px;
        // background-color: palegoldenrod;

        .foot-item-title {
          margin-top: 30px;
          font-size: 14px;
          font-weight: 300;
          color: rgba(7, 2, 55, 1);
          height: 30px;
        }

        .foot-item-content {
          font-size: 12px;
          font-weight: 200;
          color: rgba(148, 145, 170, 1);
          height: 26px;

          a {
            text-decoration: none;
            font-size: 12px;
            font-weight: 200;
            color: rgba(148, 145, 170, 1);
            transition: 0.3s;

            &:hover {
              color: #D5A24E;
            }
          }
        }

      }

      .icom-a {
        a {
          margin-right: 15px;
        }
      }
    }
  }
}