// < 767

@media screen and (max-width: 767px) {
  .item-img-come {
    width: 40px;
    height: 40px;
    background: #D9D8E0;
    border-radius: 50%;
    padding-top: 10px;
    text-align: center;
    font-size: 10px;
    font-weight: 500;
    color: rgba(109, 107, 121, 1);
    line-height: 12px;

    img {
      // width: 80%;
    }
  }

  .margin0a {
    margin: 0 auto !important;
    margin-bottom: 3px !important;
  }

  .mt40 {
    margin-top: 20px !important;
  }

  .title {
    width: 100%;
    text-align: center;
    // background-color: green;
    margin: 0 auto;
    font-size: 14px;
    font-weight: bold;
    color: rgba(7, 2, 55, 1);
    height: 40px;
    line-height: 40px;
    position: relative;

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 40px;
      height: 3px;
      left: 50%;
      bottom: 0px;
      background-color: #D5A24E;
      transform: translate(-40%, 0%);
    }
  }

  .card-wrap {
    width: 90%;
    margin: 0 auto;
    // background-color: red;

    .card-item {
      width: 100%;
      border: 1px solid #E9E8F2;
      padding-left: 15px;
      padding-right: 15px;
      margin-bottom: 15px;

      .card-item-title {
        font-size: 18px;
        font-weight: bold;
        color: rgba(7, 2, 55, 1);
        height: 50px;
        line-height: 55px;
        position: relative;
        width: 150px;
        // background-color: red;
        margin: 0 auto;
        text-align: center;

        &::after {
          content: '';
          display: block;
          position: absolute;
          width: 30px;
          height: 3px;
          left: 50%;
          bottom: 0px;
          background-color: #D5A24E;
          transform: translate(-50%, 0%);
        }
      }

      .card-item-logo {
        // background-color: greenyellow;
        padding-top: 15px;

        .logo-wrap {
          width: 30%;
          height: 40px;
          float: left;
          background-color: #F4F2FB;
          margin-right: 5%;
          margin-bottom: 15px;
          position: relative;
          border-radius: 24px;

          img {
            width: 60%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }

        .logo-wrap:nth-child(3n) {
          margin-right: 0;
        }
      }
    }
  }

  .Media-reports {
    padding-top: 40px;
    width: 100%;
    margin: 0 auto;

    // background-color: red;
    .title {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: bold;
      color: rgba(7, 2, 55, 1);
      height: 80px;
      line-height: 55px;
      position: relative;
      width: 150px;
      // background-color: red;
      margin: 0 auto;
      text-align: center;

      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 30px;
        height: 3px;
        left: 50%;
        bottom: 30px;
        background-color: #D5A24E;
        transform: translate(-50%, 0%);
      }
    }

    .Media-item {
      width: 90%;
      margin: 0 auto;
      height: 60px;
      // line-height: 60px;
      border-bottom: 1px solid #DDDCEB;
      // background-color: red;
      position: relative;

      .time {
        font-size: 12px;
        font-weight: 200;
        color: rgba(148, 145, 170, 1);
        display: block;
        padding-top: 10px;
        padding-bottom: 6px;
        // float: left;
        // width: 24%;
        // background-color: red;
      }

      .news {
        font-size: 14px;
        font-weight: 300;
        color: rgba(7, 2, 55, 1);
        width: 80%;
        display: block;
        // float: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        // background-color: red;
        // display: -webkit-box;
        // -webkit-box-orient: vertical;
        // -webkit-line-clamp: 1;
        // display: inline-block;
        // overflow: hidden;
      }

      .pic {
        position: absolute;
        right: 0;
        top: 20px;

        img {
          width: 8px;
        }
      }
    }
  }

  .protocols-wrap {
    margin-top: 40px;
    // padding-top: 10px;
    padding-bottom: 80px;
    width: 100%;
    // background-color: green;
    background: url('./images/bj-pro.png') no-repeat;
    background-size: 2000%;
  }

  .protocols {
    width: 90%;
    margin: 0 auto;
    // background-color: red;
    padding-top: 20px;

    .protocols-monetary {
      .item-wrap {
        .item {
          margin-top: 10px;
          width: 100%;
          background-color: #fff;
          padding: 10px;
          background: rgba(255, 255, 255, 1);
          box-shadow: 5px 9px 30px 0px rgba(52, 52, 92, 0.08);
          border-radius: 6px;

          .item-img {
            img {
              width: 40px;
            }
          }

          .item-title {
            height: 20px;
            line-height: 25px;
            font-size: 14px;
            font-weight: bold;
            color: rgba(7, 2, 55, 1);
          }

          .item-title-plus {
            font-size: 12px;
            font-weight: 500;
            height: 28px;
            color: rgba(7, 2, 55, 1);
          }

          .item-details {
            font-size: 12px;
            font-weight: 500;
            color: rgba(148, 145, 170, 1);
            line-height: 16px;
            height: 60px;
          }

          .item-arrow {
            cursor: pointer;
            padding-top: 10px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(215, 167, 88, 1);

            img {
              margin-left: 10px;
              width: 30px;
            }
          }
        }

        .item:nth-child(2n) {
          margin-right: 0;
        }
      }


    }

    .protocols-liquidity {
      .item-wrap {
        .item {
          margin-top: 10px;
          background: rgba(255, 255, 255, 1);
          box-shadow: 5px 9px 30px 0px rgba(52, 52, 92, 0.08);
          border-radius: 6px;

          width: 100%;
          background-color: #fff;
          padding: 10px;

          .item-img {
            height: 40px;
            text-align: center;

            img {
              width: 40px;
            }
          }

          .item-title {
            text-align: center;
            height: 25px;
            line-height: 25px;
            font-size: 14px;
            font-weight: bold;
            color: rgba(7, 2, 55, 1);
          }

          .item-title-plus {
            text-align: center;
            font-size: 12px;
            font-weight: 500;
            height: 30px;
            color: rgba(7, 2, 55, 1);
          }

          .item-details {
            text-align: center;
            font-size: 12px;
            font-weight: 500;
            color: rgba(148, 145, 170, 1);
            line-height: 16px;
            height: 54px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
          }

          .item-arrow {
            text-align: center;
            cursor: pointer;
            padding-top: 10px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(215, 167, 88, 1);

            img {
              margin-left: 10px;
              width: 30px;
            }
          }
        }

        .item:nth-child(3n) {
          margin-right: 0;
        }
      }
    }

  }
}